import HeaderComponent from "@/components/HeaderComponent";
import FooterComponent from "@/components/FooterComponent";
import StepOne from "@/components/orders/StepOne";
import StepTwo from "@/components/orders/StepTwo";
import StepThree from "@/components/orders/StepThree";
import StepFour from "@/components/orders/StepFour";

export default {
  name: "Order",
  components: {
    HeaderComponent,
    FooterComponent,
    StepOne,
    StepTwo,
    StepThree,
    StepFour,
  },

  data: () => ({
    appName: "Need for drive",
    currentStep: 1,
    options: [],
    price: {
      priceMin: 0,
      priceMax: 0,
      total: 0,
    },
    fields: {
      city: {
        value: null,
        name: "Город",
        rules: "required",
        stepId: 1,
      },
      point: {
        value: null,
        name: "Пункт выдачи",
        rules: "required",
        stepId: 1,
      },
      product: {
        value: null,
        name: "Модель",
        rules: "required",
        stepId: 2,
      },
      color: {
        value: null,
        name: "Цвет",
        rules: "required",
        stepId: 3,
      },
      dateFrom: {
        value: null,
        name: "Начало аренды",
        rules: "required",
        stepId: 3,
      },
      dateTo: {
        value: null,
        name: "Конец аренды",
        rules: "required|after:@dateFrom",
        stepId: 3,
      },
      rate: {
        value: null,
        name: "Тариф",
        rules: "required",
        stepId: 3,
      },
      isFullTank: {
        value: null,
        name: "Полный бак",
        stepId: 3,
      },
      isNeedChildChair: {
        value: null,
        name: "Детское кресло",
        stepId: 3,
      },
      isRightWheel: {
        value: null,
        name: "Правый руль",
        stepId: 3,
      },
    },
    steps: [
      {
        id: 1,
        name: "Местоположение",
        buttonActionName: "Выбрать модель",
      },
      {
        id: 2,
        name: "Модель",
        buttonActionName: "Дополнительно",
      },
      {
        id: 3,
        name: "Дополнительно",
        buttonActionName: "Итого",
      },
      {
        id: 4,
        name: "Итого",
        buttonActionName: "Заказать",
      },
    ],
  }),
  methods: {
    stepActivate() {
      return this.currentStep < this.steps.length
        ? (this.currentStep = this.currentStep + 1)
        : false;
    },
    getStepComponent(step) {
      switch (step) {
        case 1:
          return "StepOne";
        case 2:
          return "StepTwo";
        case 3:
          return "StepThree";
        case 4:
          return "StepFour";
        default:
          return "StepOne";
      }
    },
    stepEdit(stepId, valid) {
      const isEditable = (this.currentStep + 1 == stepId && valid) || stepId <= this.currentStep;
      return isEditable;
    },
    showDivider(stepId) {
      return stepId !== this.steps.length;
    },
    updateOptions(updatedOption) {
      this.options = this.options.filter((option) => {
        return (
          option.type !== updatedOption.type &&
          option.stepId <= this.currentStep
        );
      });

      this.clearFields(updatedOption.stepId);

      if (updatedOption.value) {
        this.options.push(updatedOption);
      }
    },
    updatePrice(price) {
      this.price = price
    },
    clearFields(currentStep) {
      for (let field in this.fields) {
        if (this.fields.hasOwnProperty(field)) {
          if (this.fields[field].stepId > currentStep) {
            this.fields[field].value = null;
          }
        }
      }
    },
  },
  computed: {
    updatedPrice() {
      if (this.price.total) {
        return this.price.total;
      } else if (this.price.priceMin && this.price.priceMax) {
        return `от ${this.price.priceMin} до ${this.price.priceMax}`;
      }
      return null;
    },
    actionName() {
      return this.steps.find((step) => step.id === this.currentStep)
        .buttonActionName;
    },
  },
};
